@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
    font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-height: 100%;
  max-width: 100%;
  height: 100vh;
}


.chat-title{
  color: purple;
  font-size: 2.4rem;
}
.login-form{
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
    max-width: 420px;
    width: 380px;
    height: auto;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
    grid-gap: 10px;
}

.login-btn{
    border: none;
    border-radius: 4px;
    height: 30px;
    font-size: 1.2rem;
    color: purple;
    cursor: pointer;
    transition: 0.6s ease-in-out;
    outline: purple;
}

.login-btn:hover{
    transform: scale(0.95);
}

:root {
	--dark-color-a: #667aff;
	--dark-color-b: #7386ff;
	--light-color: #e6e9ff;
	--success-color: #5cb85c;
	--error-color: #f7e7e7;
}
a{
    text-decoration: none;
}
.chat-content{
    display: grid;
    grid-template-columns: 2fr 4fr;
    justify-content: center;
    align-items: center;
    grid-gap: 5px;
}

.chat-messages{
    margin: 10px;
    padding: 5px;
    width: 100%;
    bottom: 0;
    /* overflow-y: scroll; */
}
.chat-users{
    color: purple;
}
.chat-input-msg{
  width: 100%;
}

.input-msg {
    width: 100%;
    padding: 5px;
    /* flex: 1; */
}

.chat-messages{
   max-height: 300px;
   max-width: 400px;
   width: 400px;
   height: 300px;
}
.div-messages{
    background-color: #e6e9ff;
    background-color: var(--light-color);
    border-radius: 10px;
    padding: 10px;
    margin: 5px 0;

}
.div-message{
    display: block;
    text-align: center;
    background-color: #f7e7e7;
    background-color: var(--error-color);
    border-radius: 10px;
    padding: 10px;
    margin: 5px 0;
    min-height: 70px;
    height: 70px;
    opacity: 1;
    transition: 0.6s ease-in-out;
}
.div-message.hide {
    opacity: 0;
}

.chat-time{
    color: black;
    font-weight: 800;
    font-size: 0.8rem;

}
.messages{
    color: purple;
    font-weight: 800;
    font-size:0.9rem;
}

.message{
    color: purple;
    font-weight: 800;
    font-size:0.9rem;
}

.text{
    color: rgb(83, 81, 81);
   font-size:0.9rem;
}

.chat-exit-btn{
    border: none;
    color: #ffffff;
    background: rgb(248, 99, 144);
    padding: 7px 13px;
    border-radius: 2px;
    transition: 0.6s ease-in-out;
    cursor: pointer;
    outline: none;
}
.chat-exit-btn:hover{
    transform : scale(0.98)
}

img.chat-flag{
    width: 40px;
    border-radius: 2px;
}

@media(max-width:700px){
    .chat-left{
        display: none;
    }
    .chat-right{
        margin: 10px;
    }
}
